import React from "react";
import {Link} from "react-router-dom";
import Nav from "../components/nav";

import '../index.css';
 
function Opportunities(props) {
  return (
    <div id="top">
      <Nav />

      <div className="BodyContent" id="opp">
        <h2>Finding Opportunities</h2>

        <p id="section">Classes</p>
        <p id="pp">The concept of Cognitive Science is intriguing. You analyze cyborgs, profanity, and poorly designed doors, but you also rehash 
        action potentials and neuroanatomy<a id="fn6" href="#fn-6"><sup>6</sup></a> until your brain cells become more self-aware than specialized to design think. The curriculum is on brand 
        for an interdisciplinary major, but it’s a reality check that design is only a specialization and a minor. Neither a department, nor a major of its own.</p>
        <p id="pp">Assuming you spend at least 5 hours a week per class (attending lectures and sections, completing assignments, studying, taking 
      exams) for 11 weeks, you would have spent roughly 880 hours to complete your 16 non-design classes. Compared to the 385 hours you’re expected to 
      contribute to your 7 design classes<a id="fn7" href="#fn-7"><sup>7</sup></a>, there’s clearly a reason why you feel underprepared for a design role. That’s time that could’ve been spent 
      practicing Figma, learning visual design and branding, creating a portfolio, and applying to internships - everything we have to do outside of 
      class to compensate for this curriculum imbalance.</p>
        <p id="pp">Design classes at UCSD excel in introducing the foundations of design thinking. For most classes like DSGN 1, DSGN 100, and COGS 187A, 
        you’ll work in groups to blast through the double diamond and conclude with a product that attempts to solve a problem. This holistic, quick-paced 
        approach encourages us to be well-rounded designers who take ownership of the entire design process, but very few classes emphasize how each part of 
        the design process can be further dissected.</p>
        <p className="quote">"Upper-div design classes are like DSGN 1 on steroids."</p>
        <p id="pp">Have you ever taken a class on content writing, motion design, product management, storytelling, or user research methods? 
        How about design ethics, accessibility, or privacy? Surely you’ve taken a class where everyone in your group project is the UX Designer, 
        and your final project doesn’t make it to your portfolio. Design careers carry the responsibility of advocating for the user, so why aren’t 
        we more prepared to discuss our intentions and impact of our work? Seeds of imposter syndrome are sewn into our psyche when we compare ourselves 
        to student designers from other schools and realize the limits of our self-taught skills. Classes should help us learn what matters to us, but the 
        lack of exposure to our career options forges a narrow path: become a UX Designer or perish.</p>
        <p id="pp">There are great design classes like DSGN 118, 160, COGS 127, and 128, but they’re only offered <u>once a year.</u> In fact, with the 
        exception of DSGN 1 and DSGN 100, all our design electives are offered once a year during a specific quarter. No one tells you during 
        orientation to frequently consult with the course offerings schedule<a id="fn8" href="#fn-8"><sup>8</sup></a>, but it’s a survival skill you need. If you have a later enrollment 
        time or need to prioritize other classes on your first-pass, you’re fucked.</p>
        <p className="quote">"Waitlists are huge, and transfers are at a disadvantage with later enrollment times."</p>
        <p id="pp">For transfers who feel a time pressure to make the most of their 
        two years, the limited class offerings and long waitlists are demoralizing. If you can’t get into COGS 121 or 127 due to high demand, you have 
        to wait another year to get a supportive classroom environment that prepares you for job hunting or muster what little motivation you have to 
        write case studies and reach out to people for feedback on your own time. The disparity is disheartening.</p>
        <p id="pp">Classes are a great starting point, but we can’t entirely rely on them to reach our north stars. Our curriculum leans heavily 
        toward generalized Cognitive Science principles, design classes lack diversity in topics, and classes that do offer unique topics have sparse availability. 
        Our formal education alone isn’t enough to prepare us, but we can’t wait for the department to implement the changes we need.</p>

        <p id="section">Extracurriculars</p>
        <p id="pp">As job seekers, we’re expected to advertise our inherent uniqueness and design acumen through our experiences. Excessive pressure 
        is placed on extracurriculars to provide the experiences we lack from our education and distinguish ourselves as applicants. Getting involved 
        is especially complicated if you’re unsure of how to find relevant opportunities at a large public university<a id="fn9" href="#fn-9"><sup>9</sup></a>.</p>
        <p id="pp">If you’re first-gen, you hope to receive support as you balance the expectations imposed on you and strive to live instead of 
        simply surviving. If you’re a transfer and/or recently switched into the major, you want to know the path of least resistance to make the 
        most of your time here. If you’re an international student or out-of-state, you need to ensure that your investment in UCSD yields 
        opportunities, experiences, and work visas that make the hefty tuition costs worth it.</p>
        <p id="pp">To save you some hours of LinkedIn stalking, you should know that design students usually find opportunities by asking others 
        for referrals or saying yes to any opportunity that falls in their radar (emails, newsletters, Facebook posts, guest speakers, 
        club recruitment, etc.).</p>
        <p id="pp"><strong>Typical on-campus jobs</strong> with UX or graphic-related responsibilities include:</p>
        <ul>
          <li>ArtPower</li>
          <li>University Center</li>
          <li>Geisel Library</li>
          <li>RIMAC</li>
          <li>Student councils from the seven colleges (Sixth/TMC/ERC, etc.)</li>
          <li>Resident Assistant</li>
          <li>Instructional Apprenticeship (unpaid)</li>
          <li>The Basement startups (usually unpaid)</li>
        </ul>
        <p id="pp"><strong>Research opportunities with The Design Lab</strong> and its partners are commonly found through:</p>
        <ul>
          <li>Professor recruitment at the end of the quarter for continued interest in class material</li>
          <li>Professor office hours</li>
          <li>Recruitment forms on Facebook and Slack</li>
          <li>Referrals from students currently in a lab</li>
          <li>Guest lectures</li>
          <li>Contact with current researchers via email</li>
        </ul>
        <p id="pp"><strong>Independent research</strong> under a professor generally comes from establishing a rapport with them through office 
        hours or being an IA.</p>
        <p id="pp"><strong>Student orgs</strong> like</p>
        <ul>
          <li>Design Co</li>
          <li>Design for America (DFA)</li>
          <li>Hue Collective</li>
          <li>Cognitive Science Student Association (CSSA)</li>
          <li>AdWave</li>
          <li>Product Space</li>
          <li>Triton Software Engineering (TSE)</li>
          <li>Association for Computing Machinery (ACM)</li>
          <li>Women in Computing (WIC)</li>
        </ul>
        <p id="pp">And even non-design orgs provide leadership roles or host GBMs that facilitate your growth as a student designer and cultivate a sense of community.</p>
        <p id="pp">In addition to any involvement, our side job includes navigating Figma, Medium articles, website builders, portfolios, 
        case studies, mentorships, networking, and personal projects. We’re taking ownership of our college education despite the lack of career 
        preparation we feel from our classes.</p>
        <p id="pp">Call it resilience or resourcefulness - we’re willing to do the best we can with what we have. The plethora of extracurricular 
        opportunities yields optimism at best, but this self-service approach to design resources is an accessibility problem. We don’t know what 
        we don’t know.</p>
        <p id="pp">We directly experience the disproportion of students to available seats, leadership roles, and research projects. These lost 
        opportunities then manifest into a lack of awareness and confidence about design job hunting etiquette. Without active outreach to students, 
        it feels lonely sometimes when we’re left to forage for these fragments of knowledge.</p>        
      </div>

      <div className="footnote">
        <p id="fn-6">6. Tip: Take PSYC 106 and 108 to satisfy the COGS 187 requirements if you can.&ensp;<a className="outLink" href="https://cogsci.ucsd.edu/undergraduates/major/cbn.html#Upper-Division-Requirements-(12" 
          target="_blank" rel="noopener noreferrer"> They're equivalent to COGS 187B and 187C respectively &#8599;</a> <a className="outLink" href="https://catalog.ucsd.edu/courses/PSYC.html" 
          target="_blank" rel="noopener noreferrer"> and only require upper-div standing, as opposed to taking COGS 17 and 187A/B/C in sequence &#8599;</a>&ensp;
        <a href="#fn6">(🔎)</a>
        </p> 
        <p id="fn-7">7.&ensp;<a className="outLink" href="https://cogsci.ucsd.edu/undergraduates/major/design-interaction.html#Major-Requirements" 
          target="_blank" rel="noopener noreferrer">Cognitive Science with a Specialization in Design and Interaction Major Requirements &#8599;</a>&ensp;
        <a href="#fn7">(🔎)</a>
        </p>
        <p id="fn-8">8.&ensp;<a className="outLink" href="https://cogsci.ucsd.edu/undergraduates/courses/index.html" 
          target="_blank" rel="noopener noreferrer">Tentative Course Offerings for COGS and DSGN 2021-22 &#8599;</a>&ensp;
        <a href="#fn8">(🔎)</a>
        </p>
        <p id="fn-9">9. 31,842 total undergraduates, 2,098 enrolled in Cognitive Science |&ensp;
        <a className="outLink" href="https://ir.ucsd.edu/undergrad/publications/ug-student-profile.html" 
          target="_blank" rel="noopener noreferrer">UCSD Student Profile 2021-22 &#8599;</a>&ensp;
        <a href="#fn9">(🔎)</a>
        </p>
      </div>

      <div className="bottomNav">
        <div>
        <Link to="/NorthStars">← North Stars</Link>
        </div>
        <div>
        <a href="#top">Scroll to Top</a>
        </div>

        <div>
        <Link to="/BehindTheGrind">Behind the Grind →</Link>
        </div>
      </div>
    </div>
  );
}
 
export default Opportunities;